

























import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';

@Component
export default class CUrlEncoder extends Vue {
  // Props
  @Prop() public orgUrl?: string | undefined;
  @Prop() public resultUrl?: string | undefined;
  @Prop() public endecode?: string | undefined;

  // Data
  private dataEnDecode: string = 'encode';
  private dataOrgUrl: string = '';
  private dataResultUrl: string = '';

  // Emit
  @Emit('encode-text')
  private emitEncodeText(): void {}

  // Watch
  @Watch('dataEnDecode')
  private watchOnEnDecodeSelected(newValue: string, orgValue: string) {
    this.endecodeUrl();
  }
  @Watch('dataOrgUrl')
  private watchOnTagChecked(newValue: string, orgValue: string) {
    this.endecodeUrl();
  }

  // Computed
  private get computedIsDisabled(): boolean {
    return this.dataOrgUrl === '';
  }

  // Methods
  private listenEnDecodeSelect($event: Event): void {
    this.dataEnDecode = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenInput($event: Event): void {
    this.dataOrgUrl = (<HTMLInputElement>(<any>$event).target).value;
  }
  private endecodeUrl(): void {
    switch (this.dataEnDecode) {
      case 'encode':
        this.dataResultUrl = encodeURI(this.dataOrgUrl);
        break;
      case 'decode':
        this.dataResultUrl = decodeURI(this.dataOrgUrl);
        break;
    }
  }
}
